.register__submit-button {
  display: block;
  border: 0;
  margin: 0;
  padding: 0;
  margin-top: 216px;
  width: 100%;
  height: 50px;
  background-color: #fff;
  border-radius: 2px;
  font-family: 'Inter', Arial, sans-serif;
  font-weight: 400;
  font-size: 18px;
  line-height: 1.21;
  cursor: pointer;
  transition: opacity 0.5s linear;
}

.register__submit-button:hover {
  opacity: 0.8;
}
