.header__logout-link {
  display: flex;
  padding: 0;
  font-style: normal;
  font-family: 'Inter', Arial, sans-serif;
  font-weight: 400;
  font-size: 18px;
  line-height: 1.21;
  color: #A9A9A9;
  text-decoration: none;
  transition: opacity 0.5s linear;
}

.header__logout-link:hover {
  cursor: pointer;
  opacity: 0.6;
}
